import React from 'react';

// destructuring prop into name, email, id
const Card = ({ name, email, id}) => {
	const rando = () => {
		return ((Math.random() * 1000) + 1);
	}
	return (
		<div className='bg-light-green dib br3 pa3 ma2 grow bw shadow-5'>
			<img alt='robots' src={`https://robohash.org/${rando()}?200x200`} />
			<div className='tc'>
				<h2>{name}</h2>
				<p>{email}</p>
			</div>

		</div>
	);
}

export default Card;